@import url(https://fonts.googleapis.com/css2?family=Ubuntu&display=swap);
body {
  margin: 0;
  font-family: "Ubuntu", -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
    "Oxygen", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  font-family: "Open Sans", sans-serif;
}

#content {
  margin: 0 auto;
  width: 800px;
}

.first {
  float: right;
}

li {
  list-style: none;
}
h1,
h2,
h3 {
  font-family: "Ubuntu";
}
section h1 {
  margin-bottom: 0px;
  padding-bottom: 5px;
  margin-right: 10px;
  margin-top: 0.8em;
  border-bottom: 1px solid #e3e3e3;
  font-size: 1.2rem;
}

.seperator {
  margin: 0 0.5rem 0 0.5rem;
}

.name {
  font-size: 2rem;
}

section .label {
  font-weight: bold;
  width: 150px;
  text-align: right;
  padding-right: 0.5em;
  display: inline-block;
}

section .content {
  vertical-align: top;
  width: 600px;
  display: inline-block;
}

section section {
  margin-bottom: 1rem;
}

section ul {
  width: 800px;
  overflow: hidden;
  padding-left: 0;
  font-size: 0.8rem;
}

section .double li {
  float: left;
  width: 30%;
}

#contact {
  font-size: 13px;
}

#contact a {
  text-decoration: none;
}

.skills {
  display: flex;
  flex-wrap: wrap;
}
.skills .skill-item {
  /* border: 1px solid GREY; */
  padding: 5px 10px;
  margin: 5px;
  /* color: RED; */
  border-radius: 10px;
  background-color: lightgray;
}

p {
  font-size: 13px;
}

